<template>
  <div class="search">
    <div class="input--search">
      <i class="iconfont icon-search"></i>
      <input
        type="search"
        placeholder=""
        v-model="keywords">
    </div>
    <!--  -->
    <div
      class="search__result"
      v-show="keywords.length">
      <ul class="search__result__container">
        <li
          class="search__result__item border-bottom"
          v-for="city in result"
          :key="city.id"
          @click="handleCityItemClick(city.name)">{{city.name}}</li>
        <!--  -->
        <li
          class="search__result__item border-bottom"
          v-show="result.length === 0"></li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  name: 'CitySearch',
  props: {
    cities: {
      type: [Object, Array],
      default: () => []
    }
  },
  data () {
    return {
      keywords: '',
      timer: null,
      result: []
    }
  },
  watch: {
    keywords () {
      // 
      if (this.keywords.length === 0) {
        this.result = []
        return
      }
      // 
      if (this.timer) {
        clearTimeout(this.timer)
      }
      this.timer = setTimeout(() => {
        let tempRes = []
        for (const i in this.cities) {
          const list = this.cities[i]
          if (list) {
            list.forEach((city) => {
              if (city && city.id && (city.spell.indexOf(this.keywords) > -1 || city.name.indexOf(this.keywords) > -1)) {
                tempRes.push(city)
              }
            })
          }
        }
        this.result = tempRes
      }, 30)
    }
  },
  methods: {
    ...mapMutations(['changeCurrentCity']),
    handleCityItemClick (city) {
      //  vuex state  dispatch  action
      // this.$store.commit('changeCurrentCity', city)
      //  mapMutations
      this.changeCurrentCity(city)
      // 
      this.$router.push('/')
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~styles/mixins.scss';

.search {
  //  header 
  @include header;
  .input--search {
    @include input--search;
    input {
      text-align: center;
    }
  }
  // 
  &__result {
    position: absolute;
    // 
    top: .87rem;
    right: 0;
    bottom: 0;
    left: 0;
    //  List.vue 
    z-index: 1;
    overflow: hidden;
    background: #eee;
    &__container {
      @include reset--list;
      //  better-scroll 
      height: 100%;
      overflow-y: auto;
    }
    &__item {
      line-height: .3rem;
      text-indent: .1rem;
      color: #666;
      background: #fff;
    }
  }
}
</style>



